<template>
  <div>
    <h1 class="title">Dictionary</h1>
    <h2 class="subtitle">{{ language | humanizeLanguage }}</h2>

    <table class="table">
      <tbody>
        <tr v-for="lemma in dictionary">
          <td><a v-link="{ name: 'lemma', params: { language: language, lemma: lemma.id }}"><span :lang="language">{{ lemma.lemma }}</span> ({{ lemma.part_of_speech | humanizePartOfSpeech }})</a> {{ lemma.glosses.eng }}</td>
        </tr>
      </tbody>
    </table>

    <pagination :per-page="20" :on-change="changePage" :total="total" :current-page="page"></pagination>
  </div>
</template>

<script>
import Pagination from './ui/Pagination'

import api from './api'
import filterMixin from './filters'

export default {
  name: 'DictionaryView',

  mixins: [filterMixin],

  components: {
    Pagination
  },

  data() {
    return {
      schema: {},
      dictionaries: [],

      page: 1,
      total: 0,
      dictionary: []
    }
  },

  computed: {
    language() {
      return this.$route.params.language
    }
  },

  route: {
    data({ to, abort }) {
      this.page = +to.query.page || 1

      let dictionaryParams = {
        language: to.params.language,
        part_of_speech: to.query.part_of_speech,

        page: this.page
      }

      return Promise.all([
        api.getDictionary(dictionaryParams),
        api.getSchema(),
        api.getDictionaries()
      ]).then(function(data) {
        return {
          total: data[0].total,
          dictionary: data[0].data,
          schema: data[1],
          dictionaries: data[2]
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  },

  methods: {
    changePage(page) {
      let newQuery = this.$route.query
      newQuery.page = +page || 1

      this.$router.go({
        name: 'dictionary',
        params: this.$route.params,
        query: newQuery
      })
    }
  }
}
</script>



