<template>
  <div>
    <h3 class="subtitle">Translations</h3>
    <div class="content" :lang="$key" v-for="gloss in glosses">
      {{ gloss }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'LemmaDefinitonView',

  computed: {
    glosses() {
      return this.$parent.lemma.glosses || {}
    }
  }
}
</script>



